.user {
    @media screen and (min-width: 1280px) {
        position: fixed;
    }       
}

.user__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 40px;
    padding: 20px 8px 24px 8px;
    background-color: #ffffff;
    box-shadow: 3px 8px 14px rgba(136, 198, 253, 0.19);
    border-radius: 20px;

    @media screen and (min-width: 768px) {
        flex-direction: row-reverse;
        padding: 20px 76px 20px 20px;
        gap: 71px;
        border-radius: 40px;
    }

    @media screen and (min-width: 1280px) {
        flex-direction: column;
        padding: 20px 24px 20px 16px;
        border-radius: 40px;
        width: 395px;
        gap: 0;
    }
}

.user__title {
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 1.35;
    margin-bottom: 18px;
    color: #000000;

    @media screen and (min-width: 768px) {
        font-size: 28px;
        color: #111111;
        margin-bottom: 24px;
    }
}

.user__photo {
    margin-bottom: 14px;
}

.button {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 11px;
    width: 94px;
    padding: 0;
    background-color: transparent;
    border: none;
    font-family: inherit;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 1.83;
    margin-bottom: 22px;
    cursor: pointer;
}

.previewWrap {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.preview {
    margin-bottom: 14px;
    width: 182px;
    height: 182px;
    border-radius: 40px;
    overflow: hidden;
    cursor: pointer;
}

.inputContainer {
    width: 100%;
}

.inputWrap {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-bottom: 21px;

    @media screen and (min-width: 768px) {
        margin-bottom: 13px;
    }
}

.link {
    display: flex;
    align-items: center;
    width: 100px;
    gap: 12px;
    text-decoration: none;
    color: #888888;
    align-self: self-start;
}

.logoutIcon {
    stroke: #54ADFF;
    stroke-width: 1.5px;
    stroke-linecap: round;
    stroke-linejoin: round;
}

.errorWrap {
    /* height: 12px; */
    position: absolute;
    top: -12px;
    /* left: 50%; */
    /* transform: translate(-30%); */
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
    line-height: 1.33;
    color: #F43F5E;
    ;
}

.modalTitle {
    font-weight: 500;
    font-size: 24px;
    line-height: 1.38;
    letter-spacing: 0.04em;
    color: #111111;
    margin-bottom: 48px;

    @media screen and (min-width: 768px) {
        font-size: 36px;
        line-height: 1.36;
    }
}

.confirmIcon {
    stroke: #54ADFF;;
}

.buttonWrap {
    display: flex;
    gap: 17px;
}

.modalBtn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 107px;
    padding: 8px 0;
    gap: 8px;
    border-radius: 40px;
    border-radius: 40px;
    border: none;
    outline: none;
    font-family: inherit;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.38;
    letter-spacing: 0.04em;
    cursor: pointer;
    /* transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1); */

    @media screen and (min-width: 768px) {
        width: 129px;
        padding: 9px 35px;
    }
}

.modalBtn:hover {
    background: linear-gradient(290.46deg, #419EF1 0%, #9BD0FF 107.89%);
}

.cancelBtn {
    color: #54ADFF;
    background-color: #FFFFFF;
    border: 2px solid #54ADFF;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.cancelBtn:hover {
    color: #FEF9F9
}

.approveBtn {
    color: #FEF9F9;
    background-color: #54ADFF;

}

.logoutIconModal {
    stroke: #FEF9F9;
    stroke-width: 1.5px;
    stroke-linecap: round;
    stroke-linejoin: round;
}